.form {
	display: flex;
	align-items: center;
	gap: 8px;
	flex-direction: column;
	margin-bottom: 32px;
}

.field {
	flex-grow: 4;
	padding: 8px;
	font: inherit;
}

.field:focus {
	outline-color: #1976d2;
}

.btn {
	display: inline-flex;
	height: 56px;
	padding: 8px 12px;
	border-radius: 4px;
	border: none;
	font: inherit;
	cursor: pointer;
	background-color: #e2e5e8;
	color: inherit;
	justify-content: center;
	align-items: center;
	margin: 0 auto;
	transition: background-color 200ms linear;
}

.btn:hover,
.btn:focus {
	background-color: #1976d2;
	color: #fff;
}

@media screen and (min-width: 768px) {
	.form {
		max-width: 768px;
		flex-direction: row;
	}
}

@media screen and (min-width: 1024px) {
	.form {
		max-width: 1024px;
	}
}
